mber">51</span><br><span class="line-number">52</span><br><span class="line-number">53</span><br><span class="line-number">54</span><br><span class="line-number">55</span><br><span class="line-number">56</span><br><span class="line-number">57</span><br><span class="line-number">58</span><br><span class="line-number">59</span><br><span class="line-number">60</span><br><span class="line-number">61</span><br><span class="line-number">62</span><br><span class="line-number">63</span><br><span class="line-number">64</span><br><span class="line-number">65</span><br><span class="line-number">66</span><br><span class="line-number">67</span><br><span class="line-number">68</span><br><span class="line-number">69</span><br><span class="line-number">70</span><br><span class="line-number">71</span><br><span class="line-number">72</span><br><span class="line-number">73</span><br><span class="line-number">74</span><br><span class="line-number">75</span><br><span class="line-number">76</span><br><span class="line-number">77</span><br><span class="line-number">78</span><br><span class="line-number">79</span><br><span class="line-number">80</span><br><span class="line-number">81</span><br><span class="line-number">82</span><br><span class="line-number">83</span><br><span class="line-number">84</span><br><span class="line-number">85</span><br><span class="line-number">86</span><br><span class="line-number">87</span><br><span class="line-number">88</span><br><span class="line-number">89</span><br><span class="line-number">90</span><br></div></div><h2 id="_2-上传到服务器并解压" tabindex="-1"><a class="header-anchor" href="#_2-上传到服务器并解压" aria-hidden="true">#</a> 2.上传到服务器并解压</h2>
<p>下载好以后上传文件到server，解压缩</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token builtin class-name">cd</span> /
rz –y

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token builtin class-name">cd</span> /
<span class="token function">unzip</span> *1of2.zip <span class="token operator">&amp;&amp;</span> <span class="token function">unzip</span> *2of2.zip
<span class="token comment"># 会合并解压到database文件夹下</span>

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>yum list <span class="token operator">|</span> <span class="token function">grep</span> zip/unzip
yum <span class="token function">install</span> <span class="token function">zip</span>
yum <span class="token function">install</span> <span class="token function">unzip</span>

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h2 id="_3-安装依赖" tabindex="-1"><a class="header-anchor" href="#_3-安装依赖" aria-hidden="true">#</a> 3.安装依赖</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code> yum <span class="token function">install</span> gcc <span class="token function">make</span> binutils gcc-c++ compat-libstdc++-33elfutils-libelf-devel elfutils-libelf-devel-static ksh libaio libaio-develnumactl-devel sysstat unixODBC unixODBC-devel pcre-devel –y

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>或下载oracle-11g-rpm.zip并通过以下命令安装
<a href="http://www.maxium.work:8888/wp-content/uploads/files/oracle-11g-rpm.zip" target="_blank" rel="noopener noreferrer">oracle-11g-rpm.zip<OutboundLink/></a></p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">rpm</span> -ivh *.rpm --force --nodeps

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h2 id="_4-添加oracle用户" tabindex="-1"><a class="header-anchor" href="#_4-添加oracle用户" aria-hidden="true">#</a> 4.添加oracle用户</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">groupadd</span> oinstall <span class="token comment"># 添加组</span>

<span class="token function">groupadd</span> dba <span class="token comment"># 添加组</span>

<span class="token comment"># -g 不为用户创建新组， 而使用 -g 后的指定用户组，</span>
<span class="token comment"># -G 后面跟一个用户组列表，使用户属于指定的多个用户组。</span>
<span class="token function">useradd</span> -g oinstall -G dba oracle <span class="token comment"># oracle用户，并加入组 dba oracle</span>


<span class="token function">passwd</span> oracle <span class="token comment"># 修改密码</span>

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><p>可以通过以下指令查看用户</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">id</span> oracle <span class="token comment"># uid=1001(oracle) gid=1001(oinstall) groups=1001(oinstall),1002(dba)</span>

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h2 id="_5-修改内核参数配置文件" tabindex="-1"><a class="header-anchor" href="#_5-修改内核参数配置文件" aria-hidden="true">#</a> 5. 修改内核参数配置文件</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">vim</span> /etc/sysctl.conf

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>添加以下文本</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>fs.aio-max-nr <span class="token operator">=</span> <span class="token number">1048576</span>
fs.file-max <span class="token operator">=</span> <span class="token number">6815744</span>
kernel.shmall <span class="token operator">=</span> <span class="token number">2097152</span>
kernel.shmmax <span class="token operator">=</span> <span class="token number">1073741824</span> <span class="token comment"># 这里分配了最大1G内存</span>
kernel.shmmni <span class="token operator">=</span> <span class="token number">4096</span>
kernel.sem <span class="token operator">=</span> <span class="token number">250</span> <span class="token number">32000</span> <span class="token number">100</span> <span class="token number">128</span>
net.ipv4.ip_local_port_range <span class="token operator">=</span> <span class="token number">9000</span> <span class="token number">65500</span>
net.core.rmem_default <span class="token operator">=</span> <span class="token number">262144</span>
net.core.rmem_max <span class="token operator">=</span> <span class="token number">4194304</span>
net.core.wmem_default <span class="token operator">=</span> <span class="token number">262144</span>
net.core.wmem_max <span class="token operator">=</span> <span class="token number">1048576</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token comment"># 可以用这个命令检查是否写入成功</span>
sysctl -p
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h2 id="_6-修改用户限制文件" tabindex="-1"><a class="header-anchor" href="#_6-修改用户限制文件" aria-hidden="true">#</a> 6. 修改用户限制文件</h2>
<p>修改/etc/security/limits.conf文件：</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">vim</span> /etc/security/limits.conf
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>添加以下内容</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>oracle           soft    nproc           <span class="token number">2047</span>
oracle           hard    nproc           <span class="token number">16384</span>
oracle           soft    nofile          <span class="token number">1024</span>
oracle           hard    nofile          <span class="token number">65536</span>
oracle           soft    stack           <span class="token number">10240</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>修改/etc/pam.d/login文件：</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">vim</span> /etc/pam.d/login
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>添加以下内容</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>session required  /lib64/security/pam_limits.so
session required   pam_limits.so
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>修改/etc/profile文件：</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">vim</span> /etc/profile
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>$ <span class="token comment"># 添加此行以下内容</span>
<span class="token comment">#oracle配置</span>
<span class="token keyword">if</span> <span class="token punctuation">[</span> <span class="token environment constant">$USER</span> <span class="token operator">=</span> <span class="token string">"oracle"</span> <span class="token punctuation">]</span><span class="token punctuation">;</span> <span class="token keyword">then</span>
  <span class="token keyword">if</span> <span class="token punctuation">[</span> <span class="token environment constant">$SHELL</span> <span class="token operator">=</span> <span class="token string">"/bin/ksh"</span> <span class="token punctuation">]</span><span class="token punctuation">;</span> <span class="token keyword">then</span>
      <span class="token builtin class-name">ulimit</span> -p <span class="token number">16384</span>
      <span class="token builtin class-name">ulimit</span> -n <span class="token number">65536</span>
  <span class="token keyword">else</span>
      <span class="token builtin class-name">ulimit</span> -u <span class="token number">16384</span> -n <span class="token number">65536</span>
  <span class="token keyword">fi</span>
<span class="token keyword">fi</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div><h2 id="_7-创建安装目录" tabindex="-1"><a class="header-anchor" href="#_7-创建安装目录" aria-hidden="true">#</a> 7. 创建安装目录</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">mkdir</span> software
<span class="token function">mv</span> database/ /software/
<span class="token builtin class-name">cd</span> software
<span class="token function">mkdir</span> -p /data/oracle/product/11.2.0
<span class="token function">mkdir</span> /data/oracle/oradata
<span class="token function">mkdir</span> /data/oracle/inventory
<span class="token function">mkdir</span> /data/oracle/fast_recovery_area
<span class="token function">chown</span> -R oracle:oinstall /data/oracle
<span class="token function">chmod</span> -R <span class="token number">775</span> /data/oracle
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div><h2 id="_8-设置环境变量" tabindex="-1"><a class="header-anchor" href="#_8-设置环境变量" aria-hidden="true">#</a> 8. 设置环境变量</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">su</span> -l oracle <span class="token comment"># 切换到oracle用户</span>
<span class="token function">vim</span> .bash_profile
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>$ <span class="token comment"># 添加此行以下内容</span>
<span class="token assign-left variable">ORACLE_BASE</span><span class="token operator">=</span>/data/oracle
<span class="token assign-left variable">ORACLE_HOME</span><span class="token operator">=</span><span class="token variable">$ORACLE_BASE</span>/product/11.2.0
<span class="token assign-left variable">ORACLE_SID</span><span class="token operator">=</span>orcl
<span class="token assign-left variable"><span class="token environment constant">PATH</span></span><span class="token operator">=</span><span class="token environment constant">$PATH</span><span class="token builtin class-name">:</span><span class="token variable">$ORACLE_HOME</span>/bin:<span class="token variable">$ORACLE_HOME</span>/product/11.2.0/bin
<span class="token builtin class-name">export</span> ORACLE_BASE ORACLE_HOME ORACLE_SID <span class="token environment constant">PATH</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><p>检查ORACLE_BASE和PATH前面少不少美元符,并且更新</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token builtin class-name">source</span> .bash_profile
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>复制安装包到oracle用户根目录</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">cp</span> -R /software/database/response/ <span class="token builtin class-name">.</span>
<span class="token builtin class-name">cd</span> response/
<span class="token function">vim</span> db_install.rsp
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>修改以下属性</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>oracle.install.option<span class="token operator">=</span>INSTALL_DB_SWONLY
<span class="token assign-left variable">ORACLE_HOSTNAME</span><span class="token operator">=</span>CentOS
<span class="token assign-left variable">UNIX_GROUP_NAME</span><span class="token operator">=</span>oinstall
<span class="token assign-left variable">INVENTORY_LOCATION</span><span class="token operator">=</span>/data/oracle/inventory
<span class="token assign-left variable">SELECTED_LANGUAGES</span><span class="token operator">=</span>en,zh_CN
<span class="token assign-left variable">ORACLE_HOME</span><span class="token operator">=</span>/data/oracle/product/11.2.0
<span class="token assign-left variable">ORACLE_BASE</span><span class="token operator">=</span>/data/oracle
oracle.install.db.InstallEdition<span class="token operator">=</span>EE
oracle.install.db.DBA_GROUP<span class="token operator">=</span>dba
oracle.install.db.OPER_GROUP<span class="token operator">=</span>dba
<span class="token assign-left variable">DECLINE_SECURITY_UPDATES</span><span class="token operator">=</span>true
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><h2 id="_9-开始安装oracle数据库" tabindex="-1"><a class="header-anchor" href="#_9-开始安装oracle数据库" aria-hidden="true">#</a> 9. 开始安装oracle数据库</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token builtin class-name">cd</span> /software/database/
./runInstaller -silent -responseFile /home/oracle/response/db_install.rsp -ignorePrereq
<span class="token comment"># 如果出现Successfully Setup Software，则安装程序已经停止了。</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h2 id="_10-按照要求执行脚本" tabindex="-1"><a class="header-anchor" href="#_10-按照要求执行脚本" aria-hidden="true">#</a> 10. 按照要求执行脚本</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token punctuation">[</span>oracle@localhost database<span class="token punctuation">]</span>$ The following configuration scripts need to be executed as the <span class="token string">"root"</span> user. <span class="token comment">#必须由root用户执行</span>
 <span class="token comment">#!/bin/sh</span>
 <span class="token comment">#Root scripts to run</span>

/data/oracle/inventory/orainstRoot.sh
/data/oracle/product/11.2.0/root.sh
To execute the configuration scripts:
         <span class="token number">1</span>. Open a terminal window
         <span class="token number">2</span>. Log <span class="token keyword">in</span> as <span class="token string">"root"</span>
         <span class="token number">3</span>. Run the scripts
         <span class="token number">4</span>. Return to this window and hit <span class="token string">"Enter"</span> key to <span class="token builtin class-name">continue</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token builtin class-name">exit</span>
<span class="token function">sh</span> /data/oracle/inventory/orainstRoot.sh
<span class="token function">sh</span> /data/oracle/product/11.2.0/root.sh
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h2 id="_11-以静默模式配置监听" tabindex="-1"><a class="header-anchor" href="#_11-以静默模式配置监听" aria-hidden="true">#</a> 11. 以静默模式配置监听</h2>
<p>重新使用oracle用户登录：</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">su</span> -l oracle
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>修改监听配置文件</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>$
<span class="token function">vim</span> <span class="token variable">$ORACLE_HOME</span>/network/admin/listener.ora
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>将localhost换为域名或ip，否则可能导致启动不了监听的问题</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token comment"># listener.ora Network Configuration File: /data/oracle/product/11.2.0/network/admin/listener.ora</span>
<span class="token comment"># Generated by Oracle configuration tools.</span>

LISTENER <span class="token operator">=</span>
  <span class="token punctuation">(</span>DESCRIPTION_LIST <span class="token operator">=</span>
    <span class="token punctuation">(</span>DESCRIPTION <span class="token operator">=</span>
      <span class="token punctuation">(</span>ADDRESS <span class="token operator">=</span> <span class="token punctuation">(</span>PROTOCOL <span class="token operator">=</span> IPC<span class="token punctuation">)</span><span class="token punctuation">(</span>KEY <span class="token operator">=</span> EXTPROC1521<span class="token punctuation">))</span>
      <span class="token punctuation">(</span>ADDRESS <span class="token operator">=</span> <span class="token punctuation">(</span>PROTOCOL <span class="token operator">=</span> TCP<span class="token punctuation">)</span><span class="token punctuation">(</span>HOST <span class="token operator">=</span> localhost<span class="token punctuation">)</span><span class="token punctuation">(</span>PORT <span class="token operator">=</span> <span class="token number">1521</span><span class="token punctuation">))</span> <span class="token comment"># 这一行的localhost</span>
    <span class="token punctuation">)</span>
  <span class="token punctuation">)</span>

ADR_BASE_LISTENER <span class="token operator">=</span> /data/oracle
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br></div></div><p>接下来可以尝试启动监听</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>netca /silent /responseFile /home/oracle/response/netca.rsp
<span class="token comment"># 如果存在netca找不到问题是环境变量配置有误</span>
<span class="token comment"># netca位于$ORACLE_HOME/product/*/bin</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>一切顺利的话可以发现1521在监听</p>
<h2 id="工具失效问题" tabindex="-1"><a class="header-anchor" href="#工具失效问题" aria-hidden="true">#</a> 工具失效问题</h2>
<p>如果发现netca等工具包无响应，类似如下</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>$
<span class="token punctuation">[</span>oracle@localhost ~<span class="token punctuation">]</span>$ netca
<span class="token punctuation">[</span>oracle@localhost ~<span class="token punctuation">]</span>$
<span class="token punctuation">[</span>oracle@localhost ~<span class="token punctuation">]</span>$ lsnrctl start
<span class="token punctuation">[</span>oracle@localhost ~<span class="token punctuation">]</span>$
<span class="token comment"># 检查工具包大小是否为0</span>
ll <span class="token variable">$ORACLE_HOME</span>/bin <span class="token operator">|</span> <span class="token function">grep</span> netca <span class="token comment"># -rwxr-xr-x 1 oracle oinstall     0 Apr 21 20:48 netca</span>
ll <span class="token variable">$ORACLE_HOME</span>/bin <span class="token operator">|</span> <span class="token function">grep</span> lsnrctl <span class="token comment"># -rwxr-x--x 1 oracle oinstall   0 Apr 22 00:10 lsnrctl</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><p>为0则说明在安装oracle的时候make过程出错导致make出的工具包为空</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token comment"># 此时可以先写一个helloworld程序测试gcc</span>
<span class="token comment">#若是不报错可以排除gcc问题</span>
<span class="token builtin class-name">cd</span> ~
<span class="token builtin class-name">echo</span> <span class="token string">'#include &lt;stdio.h>'</span> <span class="token operator">></span> a.c
<span class="token builtin class-name">echo</span> <span class="token string">'void main() {'</span> <span class="token operator">>></span> a.c
<span class="token builtin class-name">echo</span> <span class="token string">'printf("Hello World !");'</span> <span class="token operator">>></span> a.c
<span class="token builtin class-name">echo</span> <span class="token string">'}'</span> <span class="token operator">>></span> a.c
gcc -o a a.c

<span class="token comment"># 若是提示</span>
a.c:1:25: fatal error: stdio.h: No such <span class="token function">file</span> or directory
 <span class="token comment">#include &lt;stdio.h></span>
                  ^
compilation terminated.
<span class="token comment"># 说明依赖的include没有找到</span>
<span class="token comment"># 需要查看/usr/include/文件夹下是否有对应的库文件</span>
<span class="token comment"># 以下relink中的报错同理，如 sys/*.h 代表 /usr/include/sys/*.h</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>$ <span class="token comment"># 当解决依赖问题后需要relink</span>
<span class="token variable">$ORACLE_HOME</span>/bin/relink <span class="token comment"># writing relink log to: /data/oracle/product/11.2.0/install/relink.log</span>

<span class="token comment"># 可以查看relink日志</span>
<span class="token function">cat</span> /data/oracle/product/11.2.0/install/relink.log
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h2 id="找不到文件问题" tabindex="-1"><a class="header-anchor" href="#找不到文件问题" aria-hidden="true">#</a> 找不到文件问题</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>$ <span class="token comment"># 如果遇到以下问题</span>
<span class="token punctuation">[</span>oracle@localhost ~<span class="token punctuation">]</span>$ netca /silent /responseFile /home/oracle/response/netca.rsp
