<span class="token function">cp</span> libclntsh.so.11.1  <span class="token variable">$ORACLE_HOME</span>/lib/
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><h2 id="_64位机器安装32位问题" tabindex="-1"><a class="header-anchor" href="#_64位机器安装32位问题" aria-hidden="true">#</a> 64位机器安装32位问题</h2>
