<span class="token comment">#如果出现以上错误需要安装包</span>
yum <span class="token function">install</span> -y psmisc
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br></div></div><h2 id="_12-以静默方式建立库" tabindex="-1"><a class="header-anchor" href="#_12-以静默方式建立库" aria-hidden="true">#</a> 12. 以静默方式建立库</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code> <span class="token function">vim</span> /home/oracle/response/dbca.rsp
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code> <span class="token comment"># 修改以下配置</span>
 <span class="token punctuation">[</span>GENERAL<span class="token punctuation">]</span>

<span class="token comment"># oracle版本，不能更改</span>
RESPONSEFILE_VERSION <span class="token operator">=</span> <span class="token string">"11.2.0"</span>

<span class="token comment"># Description   : Type of operation</span>
OPERATION_TYPE <span class="token operator">=</span> <span class="token string">"createDatabase"</span>

<span class="token punctuation">[</span>CREATEDATABASE<span class="token punctuation">]</span>

<span class="token comment"># Description   : Global database name of the database</span>
<span class="token comment"># 全局数据库的名字=SID+主机域名</span>
<span class="token comment"># 第三方工具链接数据库的时候使用的service名称</span>
GDBNAME <span class="token operator">=</span> <span class="token string">"orcl.test"</span>

<span class="token comment"># Description   : System identifier (SID) of the database</span>
<span class="token comment"># 对应的实例名字</span>
SID <span class="token operator">=</span> <span class="token string">"orcl"</span>

<span class="token comment"># Description   : Name of the template</span>
<span class="token comment"># 建库用的模板文件</span>
TEMPLATENAME <span class="token operator">=</span> <span class="token string">"General_Purpose.dbc"</span>

<span class="token comment"># Description   : Password for SYS user</span>
<span class="token comment"># SYS管理员密码</span>
SYSPASSWORD <span class="token operator">=</span> <span class="token string">"123456"</span>

<span class="token comment"># Description   : Password for SYSTEM user</span>
<span class="token comment"># SYSTEM管理员密码</span>
SYSTEMPASSWORD <span class="token operator">=</span> <span class="token string">"123456"</span>

<span class="token comment"># Description   : Password for SYSMAN user</span>
<span class="token comment"># SYSMAN管理员密码</span>
SYSMANPASSWORD <span class="token operator">=</span> <span class="token string">"123456"</span>

<span class="token comment"># Description   : Password for DBSNMP user</span>
<span class="token comment"># DBSNMP管理员密码</span>
DBSNMPPASSWORD <span class="token operator">=</span> <span class="token string">"123456"</span>

<span class="token comment"># Description   : Location of the data file's</span>
<span class="token comment"># 数据文件存放目录</span>
DATAFILEDESTINATION <span class="token operator">=</span>/data/oracle/oradata

<span class="token comment"># Description   : Location of the data file's</span>
<span class="token comment"># 恢复数据存放目录</span>
<span class="token assign-left variable">RECOVERYAREADESTINATION</span><span class="token operator">=</span>/data/oracle/fast_recovery_area

<span class="token comment"># Description   : Character set of the database</span>
<span class="token comment"># 字符集，重要!!! 建库后一般不能更改，所以建库前要确定清楚。</span>
<span class="token comment"># (CHARACTERSET = "AL32UTF8" NATIONALCHARACTERSET= "UTF8")</span>
CHARACTERSET <span class="token operator">=</span> <span class="token string">"ZHS16GBK"</span>

<span class="token comment"># Description   : total memory in MB to allocate to Oracle</span>
<span class="token comment"># oracle内存1638MB,物理内存2G*80%</span>
TOTALMEMORY <span class="token operator">=</span> <span class="token string">"1638"</span>
<span class="token comment">#------------------------------------------------------------</span>

<span class="token comment"># 然后配置</span>
dbca -silent -responseFile /home/oracle/response/dbca.rsp
<span class="token comment"># 并且进程检查</span>
<span class="token function">ps</span> -ef <span class="token operator">|</span> <span class="token function">grep</span> ora_ <span class="token operator">|</span> <span class="token function">grep</span> -v <span class="token function">grep</span>
<span class="token comment"># 查看监听状态</span>
lsnrctl status
<span class="token comment"># 日志文件</span>
<span class="token function">cat</span> /data/oracle/cfgtoollogs/dbca/orcl/orcl.log
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br><span class="line-number">34</span><br><span class="line-number">35</span><br><span class="line-number">36</span><br><span class="line-number">37</span><br><span class="line-number">38</span><br><span class="line-number">39</span><br><span class="line-number">40</span><br><span class="line-number">41</span><br><span class="line-number">42</span><br><span class="line-number">43</span><br><span class="line-number">44</span><br><span class="line-number">45</span><br><span class="line-number">46</span><br><span class="line-number">47</span><br><span class="line-number">48</span><br><span class="line-number">49</span><br><span class="line-number">50</span><br><span class="line-number">51</span><br><span class="line-number">52</span><br><span class="line-number">53</span><br><span class="line-number">54</span><br><span class="line-number">55</span><br><span class="line-number">56</span><br><span class="line-number">57</span><br><span class="line-number">58</span><br><span class="line-number">59</span><br><span class="line-number">60</span><br><span class="line-number">61</span><br><span class="line-number">62</span><br><span class="line-number">63</span><br><span class="line-number">64</span><br><span class="line-number">65</span><br><span class="line-number">66</span><br></div></div><h2 id="_13-卸载oracle" tabindex="-1"><a class="header-anchor" href="#_13-卸载oracle" aria-hidden="true">#</a> 13. 卸载Oracle</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">rm</span> -rf /opt/oracle/
<span class="token comment"># 删除“/usr/bin”下的文件。</span>
<span class="token function">rm</span> -rf /software
<span class="token function">rm</span> -rf /data/oracle
<span class="token function">rm</span> -rf /usr/bin/dbhome
<span class="token function">rm</span> -rf /usr/bin/oraenv
<span class="token function">rm</span> -rf /usr/bin/coraenv
<span class="token comment"># 删除“/etc/oratab”。</span>
<span class="token function">rm</span> -rf /etc/oratab
<span class="token comment"># 删除“/etc/oraInst.loc”。</span>
<span class="token function">rm</span> -rf /etc/oraInst.loc
<span class="token comment"># 删除oracle用户。</span>
<span class="token function">userdel</span> oracle
<span class="token comment"># 删除oinstall和dba用户组。</span>
<span class="token function">groupdel</span> oinstall
<span class="token function">groupdel</span> dba
<span class="token comment"># 将启动服务删除。</span>
<span class="token function">chkconfig</span> --del dbora
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br></div></div>